import React, {useState}  from "react";

const UpvotesSection = ({
  articleName,
  upvotes,
  setArticleInfo
}) => {

  const [disabled, setDisabled] = useState('');  
  const UpvoteArticle = async () => {
    const result = await fetch(`/api/articles/${articleName}/upvote`, {
      method: "post",
    });
    const body = await result.json();
    setArticleInfo(body);
    setDisabled(true);
  };

  const DownVoteArticle = async () => {
    const result = await fetch(`/api/articles/${articleName}/downvote`, {
      method: "post",
    });
    const body = await result.json();
    setArticleInfo(body);
    setDisabled(true);
  };

  return (
    <div>
        <div>
      <button disabled={disabled} id="upvotes-section" onClick={() => UpvoteArticle()}>Add Upvote</button>
      <p>This post has {upvotes} votes</p>
      </div>
      <button disabled={disabled} id="downvote-section" onClick={() => DownVoteArticle()}>Down vote</button>
    </div>
  );
};

export default UpvotesSection;
